___CSS_LOADER_EXPORT___.push([module.id, `body{
    background-color: black;
    color: white;
    font-family: 'Rubik', sans-serif;
    font-size: 1rem;
    line-height: 1.5;
}

.top-panel{
    margin: 10px 0;
    height: 200px;
}

.form-input{
    font-weight: 600;
    font-size: 1rem;
    line-height: 1.5;
    padding: 7px 10px;
    border: none;
    border-radius: .5em;
    background-color: #b4dcb6;
}

.color-input{
    /* padding: 1px 10px; */
    width: 50px;
    height: 30px;
    background-color: #b4dcbc;
}

.car-item{
    height: 100%;
}

.finish-line{
    display: flex;
    position: relative;
    justify-content: space-between;
}

.car-track{
    border-bottom:2px dotted white;
}

.car{
    position: absolute;
    left: 85px;
    bottom: -9px;
}

.finish-flag{
    font-size: 2em;
    margin: 0;
}

.winners{
    width: 500px;
    background-color: rgba(255, 255, 255, 0.1);
}

ul.winners-nav{
    width: 100%;
    height: 50px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 20px;
}

li{
    list-style-type: none;
}

.winners-body{
    min-width: 300px;
}

ul.winners-body-ul{
    width: 100%;
    height: 50px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 20px;
